@media screen and (max-width: 600px) {
  .logos {
    width: 400px;
    height: 230px;
    display:block;
    animation-name: opasidad;
    animation-duration: 1.5s;
    }

    .img-info__container {
      display:block;
      justify-content: left;
      max-width: 230px;
      margin: -240px 0 0 200px;
    }

    .img-info__container img {
      /* position: relative; */
      border-radius: 50%;
      width: 200px;
      height: 200px;
      animation-name: img-animation;
      animation-duration: 1.5s;
    }

} 

